"use client";

import React, { useEffect, useRef, useState } from "react";
import Image from "next/image";
import { createPortal } from "react-dom";
import { addToMyList, isInMyList, removeFromMyList } from "@/lib/myList";

import {
  FaAngleRight,
  FaBookmark,
  FaChevronLeft,
  FaChevronRight,
  FaPlayCircle,
} from "react-icons/fa";
import { CiBoxList } from "react-icons/ci";
import Details from "./Details";

const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
const IMAGE_BASE = process.env.NEXT_PUBLIC_TMDB_IMAGE;

interface Movie {
  id: number;
  title: string;
  name?: string;
  backdrop_path: string | null;
  poster_path: string | null;
  release_date: string | null;
  first_air_date: string | null;
  overview: string;
}

const CARD_WIDTH = 260;

const Gems = ({ name }: { name: string }) => {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [hoveredMovie, setHoveredMovie] = useState<Movie | null>(null);
  const [hoverRect, setHoverRect] = useState<DOMRect | null>(null);
  const [isHovering, setIsHovering] = useState(false);
  const [selectedMovie, setSelectedMovie] = useState<Movie | null>(null);

  const scrollRef = useRef<HTMLDivElement>(null);
  const hoverTimeout = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    fetch(
      `https://api.themoviedb.org/3/trending/movie/week?api_key=${API_KEY}`
    )
      .then((res) => res.json())
      .then((data) => setMovies(data.results || []))
      .catch(console.error);
  }, []);

  const closeHover = () => {
    hoverTimeout.current = setTimeout(() => {
      setIsHovering(false);
      setHoveredMovie(null);
    }, 150);
  };

  return (
    <>
      {/* ================= ROW ================= */}
      <div className=" w-full relative">
        {/* TITLE */}
        <div className="px-6 sm:px-10 md:px-20 mb-2">
          <h1 className="text-2xl font-bold text-white flex items-center gap-2">
            {name} <FaAngleRight />
          </h1>
        </div>

        {/* CAROUSEL */}
        <div className="relative px-6 sm:px-10 md:px-20 group">
          {/* LEFT BUTTON */}
          <button
            onClick={() =>
              scrollRef.current?.scrollBy({
                left: -CARD_WIDTH,
                behavior: "smooth",
              })
            }
            className="hidden md:flex absolute left-5 top-1/2 -translate-y-1/2 z-40
                       bg-black/60 text-white p-2 rounded-full
                       opacity-0 group-hover:opacity-100 transition"
          >
            <FaChevronLeft size={22} />
          </button>

          {/* RIGHT BUTTON */}
          <button
            onClick={() =>
              scrollRef.current?.scrollBy({
                left: CARD_WIDTH,
                behavior: "smooth",
              })
            }
            className="hidden md:flex absolute right-5 top-1/2 -translate-y-1/2 z-40
                       bg-black/60 text-white p-2 rounded-full
                       opacity-0 group-hover:opacity-100 transition"
          >
            <FaChevronRight size={22} />
          </button>

          {/* SCROLL CONTAINER */}
          <div
            ref={scrollRef}
            className="flex gap-4 py-10 overflow-x-hidden"
          >
            {movies.map((movie) => {
              const imagePath =
                IMAGE_BASE && (movie.backdrop_path || movie.poster_path)
                  ? `${IMAGE_BASE}${movie.backdrop_path || movie.poster_path}`
                  : "/placeholder.jpg";

              return (
                <div
                  key={movie.id}
                  className="w-[250px] flex-shrink-0"
                  onMouseEnter={(e) => {
                    if (hoverTimeout.current)
                      clearTimeout(hoverTimeout.current);
                    setIsHovering(true);
                    setHoveredMovie(movie);
                    setHoverRect(
                      e.currentTarget.getBoundingClientRect()
                    );
                  }}
                  onMouseLeave={closeHover}
                >
                  <div className="relative h-[140px] w-[240px] rounded-md  overflow-hidden bg-black">
                    <Image
                      src={imagePath}
                      alt={movie.title}
                      fill
                      className="object-cover"
                    />
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>

      {/* ================= HOVER PREVIEW ================= */}
      {hoveredMovie &&
        hoverRect &&
        isHovering &&
        createPortal(
          <div
            style={{
              top: hoverRect.top - 50,
              left: hoverRect.left,
              width: hoverRect.width,
            }}
            className="fixed z-[9999] bg-[#181818] rounded-lg shadow-2xl scale-110"
            onMouseEnter={() => {
              if (hoverTimeout.current)
                clearTimeout(hoverTimeout.current);
              setIsHovering(true);
            }}
            onMouseLeave={closeHover}
          >
            {/* IMAGE */}
            <div
              className="relative h-[150px] cursor-pointer"
              onClick={() => setSelectedMovie(hoveredMovie)}
            >
              <Image
                src={`${IMAGE_BASE}${hoveredMovie.backdrop_path}`}
                alt={hoveredMovie.title}
                fill
                className="object-cover"
              />
            </div>

            {/* DETAILS */}
            <div className="p-4 text-white">
              <p className="font-semibold mb-2">
                {hoveredMovie.title}
              </p>

              <div className="flex gap-3">
                <button className="bg-white text-black p-2 rounded-full">
                  <FaPlayCircle />
                </button>
                <button className="bg-gray-700 p-2 rounded-full">
                  <CiBoxList />
                </button>
                <button className="bg-gray-700 p-2 rounded-full">
                  <FaBookmark />
                </button>
              </div>
            </div>
          </div>,
          document.body
        )}

      {/* ================= DETAILS MODAL ================= */}
      {selectedMovie && (
        <Details
          movie={selectedMovie}
          onClose={() => setSelectedMovie(null)}
        />
      )}
    </>
  );
};

export default Gems;

















